.bg {
  width: 100%;
  height: 100%;
  display: flex;
}

.toDoSectionContainer {
  display: flex;
  gap: 30px;
  width: 100%;
  justify-content: center;
  margin-top: 20px;
  height: 90%;
}

.toDoSection {
  width: 31%;
  height: 750px;
  border: 1px solid rgba(0, 0, 0, 1);
  border-radius: 15px;
  padding: 10px;
  height: 100%;
  color: #9b9b9b;
  text-align: center;
  font-size: 12px;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  line-height: 16px;
  border: 1px solid #313131;
  background-color: rgb(21, 20, 20);
}

.notStart {
}

.notStartBadge {
  display: flex;
  align-items: center;
  width: 90px;
  gap: 5px;
  padding: 0px 5px;
}

.inProgress {
}

.inProgressBadge {
  display: flex;
  align-items: center;
  width: 70px;
  gap: 5px;
  padding: 2px 5px;
}

.done {
}

.doneBadge {
  display: flex;
  align-items: center;
  width: 70px;
  gap: 5px;
  padding: 2px 5px;
}

.addTodo {
  display: none;
  flex-direction: column;
  align-items: center;
  border: 1px solid black;
  border-radius: 10px;
  width: 100%;
  margin-top: 8px;
  padding: 10px;
  gap: 10px;
  background: #323232;
}

.addTodo input {
  width: 100%;
}

.textarea_container {
  position: relative;
  width: 100%;
}

.textarea_placeholder {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  pointer-events: none;
  opacity: 40%;
}

.textarea {
  width: 100%;
  resize: none;
  outline: none;
  background: none;
}

.textarea::-webkit-scrollbar {
  background-color: black;
}

.textarea::-webkit-scrollbar-thumb {
  background-color: grey;
  border-radius: 2px;
}

.todosContainer {
  height: 95%;
  overflow: auto;
}

.addTodoTitle {
  background: none;
}

.addTodoTitle::placeholder {
  opacity: 40%;
}

.managerSelector::-webkit-scrollbar {
  display: none;
}
